}

.box-shadow {
  font-family: cursive, sans-serif;
  background-color: #ffffff;
  color: #4e0870;
  padding: 10px;
  border-radius: 4px;
  border-color: #f7c4c3;
  /* box-shadow: 2px 2px 40px 10px #e9bea3  */
}
.homeclass {
  background-repeat: "no-repeat";
  background-size: "cover";
  max-height: 100%;
  cursor: "pointer";
}

